
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "editBankCard",
components: {
ViewBox,
XHeader,
Cell,
Group,
XInput,
XTextarea,
XSwitch,
XButton
},
created() {
if (this.$route.name === "insertBankCard") {
this.isInsert = true;
} else {
this.isInsert = false;
this.bankCardInfo = this.$route.params.bankCardInfo;
console.log(this.bankCardInfo);

}
},
data() {
return {
bankCardInfo: {
cardKind: '',
cardHolder: '',
cardId: '',
isDefault: 0
},
switchTitle: '设为默认',
isInsert: false,
isDefault: false,
};
},
methods: {
insertUserBankCard () {
this.isDefault === false ? this.bankCardInfo.isDefault = 0 : this.bankCardInfo.isDefault = 1;
if (this.isInsert) {
this.$api
.insertUserBankCard(this.bankCardInfo)
.then(data => {
if (data.code === 200) {
this.$vux.toast.text('添加成功', 'top')
this.$router.go(-1);
} else {
this.$vux.toast.text(data.message, 'top')
}
});
} else {
this.$api
.updateUserBankCard(this.bankCardInfo)
.then(data => {
if (data.code === 200) {
this.$vux.toast.text('更新成功', 'top')
this.$router.go(-1);
} else {
this.$vux.toast.text(data.message, 'top')
}
});
}
},
deleteUserBankCard () {
this.$api
.deleteUserBankCard({id: this.bankCardInfo.id})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text('删除成功', 'top')
this.$router.go(-1);
} else {
this.$vux.toast.text(data.message, 'top')
}
});
}

}
};



